import React from 'react';
import Typography from '@material-ui/core/Typography';
import guideVideo from '../assets/guide.mp4';

function Download(){

    return (
        <div style={{ textAlign: 'left', paddingLeft:'80px'}}>
            <Typography variant="h2" component="h2">
            Download Autoquit
            </Typography>
            <br />
            <Typography variant="h6" component="h6">
            The application is totally free and there will never have fee or subscription in future.
            </Typography>
            <Typography variant="h6" component="h6">
            To download autoquit version 1.0.0.1, please select one of these below links. For 64-bit operating system, please download x64 version.
            </Typography>
            <br/>
            <Typography variant="body1" component="div">
            Version different: If you are using 64-bit operating system, but using 32-bit version of Autoquit, some icon will be missing or sometime cannot interact with 64-bit only programs.
            </Typography>
            <br/>
            <Typography variant="body1" component="span">
                Google Drive: <a href='https://drive.google.com/open?id=13kbpRuHday-tPpWhQG0kSf2LteV9XMa1' target='_blank' rel="noopener noreferrer">x86 </a> 
                | 
                <a href='https://drive.google.com/open?id=1EqG3w6oE8Eg1UARPuByM4NWlyRj0eKNm' target='_blank' rel="noopener noreferrer">x64</a> <br/>
                Mega.nz: <a href='https://mega.nz/#!gwxGHAwL!esWNPQpgRAf6aAwqeBC0Is2Kzs39UaEV52rKJQYi4Ss' target='_blank' rel="noopener noreferrer">x86 </a> 
                 | 
                 <a href='https://mega.nz/#!J85kAaBD!annY6ArMbm_Tog2gh8TljKIOeYt00pxNNWLaBXyivY0' target='_blank' rel="noopener noreferrer">x64</a> 
            </Typography>
            <br /> <br/>
            <Typography variant="h6" component="h6">
            Need a guide how to use? Here it is: 
            </Typography>
            <video src={guideVideo} controls></video>
        </div>
    )
}

export default Download;